import React from 'react';
import Ability_Blank from '../../images/Ability_Blank.png';
import Boost_Blank from '../../images/Boost_Blank.png';
import Setback_Blank from '../../images/Setback_Blank.png';
import Difficulty_Blank from '../../images/Difficulty_Blank.png';
import Proficiency_Blank from '../../images/Proficiency_Blank.png';
import Challenge_Blank from '../../images/Challenge_Blank.png';
import Force_Blank from '../../images/Force_Blank.png';

interface DiceProps {
  variant:
    | 'boost'
    | 'setback'
    | 'ability'
    | 'difficulty'
    | 'proficiency'
    | 'challenge'
    | 'force';
}

const Dice = ({ variant }: DiceProps) => {
  const getSelectedDice = () => {
    switch (variant) {
      case 'boost':
        return Boost_Blank;
      case 'setback':
        return Setback_Blank;
      case 'ability':
        return Ability_Blank;
      case 'difficulty':
        return Difficulty_Blank;
      case 'proficiency':
        return Proficiency_Blank;
      case 'challenge':
        return Challenge_Blank;
      case 'force':
        return Force_Blank;
      default:
        break;
    }
  };

  return (
    <img
      height='20rem'
      src={getSelectedDice()}
      style={{ rotate: variant === 'proficiency' ? '180deg' : '0deg' }}
    />
  );
};

export default Dice;
