import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Container from 'react-bootstrap/Container';
import './App.css';
import pluralize from 'pluralize';
import Dice from '../Dice/Dice';

declare type DiceSide = {
  success: number;
  advantage: number;
  failure: number;
  threat: number;
  triumph: number;
  despair: number;
  dark: number;
  light: number;
};

declare type Dice = { sides: DiceSide[] };

const BLANK: DiceSide = {
  success: 0,
  advantage: 0,
  failure: 0,
  threat: 0,
  triumph: 0,
  despair: 0,
  dark: 0,
  light: 0,
};
const SUCCESS: DiceSide = { ...BLANK, success: 1 };
const SUCCESS_ADVANTAGE: DiceSide = { ...BLANK, success: 1, advantage: 1 };
const SUCCESS_SUCCESS: DiceSide = { ...BLANK, success: 2 };
const ADVANTAGE: DiceSide = { ...BLANK, advantage: 1 };
const ADVANTAGE_ADVANTAGE: DiceSide = { ...BLANK, advantage: 2 };
const FAILURE: DiceSide = { ...BLANK, failure: 1 };
const FAILURE_FAILURE: DiceSide = { ...BLANK, failure: 2 };
const THREAT: DiceSide = { ...BLANK, threat: 1 };
const THREAT_THREAT: DiceSide = { ...BLANK, threat: 2 };
const FAILURE_THREAT: DiceSide = { ...BLANK, failure: 1, threat: 1 };
const TRIUMPH: DiceSide = { ...BLANK, triumph: 1 };
const DESPAIR: DiceSide = { ...BLANK, despair: 1 };
const DARK: DiceSide = { ...BLANK, dark: 1 };
const DARK_DARK: DiceSide = { ...BLANK, dark: 2 };
const LIGHT: DiceSide = { ...BLANK, light: 1 };
const LIGHT_LIGHT: DiceSide = { ...BLANK, light: 2 };

const dice: {
  boost: Dice;
  setback: Dice;
  ability: Dice;
  difficulty: Dice;
  proficiency: Dice;
  challenge: Dice;
  force: Dice;
} = {
  boost: {
    sides: [
      BLANK,
      BLANK,
      SUCCESS,
      SUCCESS_ADVANTAGE,
      ADVANTAGE_ADVANTAGE,
      ADVANTAGE,
    ],
  },
  setback: {
    sides: [BLANK, BLANK, FAILURE, FAILURE, THREAT, THREAT],
  },
  ability: {
    sides: [
      BLANK,
      SUCCESS,
      SUCCESS,
      SUCCESS_SUCCESS,
      ADVANTAGE,
      ADVANTAGE,
      SUCCESS_ADVANTAGE,
      ADVANTAGE_ADVANTAGE,
    ],
  },
  difficulty: {
    sides: [
      BLANK,
      FAILURE,
      FAILURE_FAILURE,
      THREAT,
      THREAT,
      THREAT,
      THREAT_THREAT,
      FAILURE_THREAT,
    ],
  },
  proficiency: {
    sides: [
      BLANK,
      SUCCESS,
      SUCCESS,
      SUCCESS_SUCCESS,
      SUCCESS_SUCCESS,
      ADVANTAGE,
      SUCCESS_ADVANTAGE,
      SUCCESS_ADVANTAGE,
      SUCCESS_ADVANTAGE,
      ADVANTAGE_ADVANTAGE,
      ADVANTAGE_ADVANTAGE,
      TRIUMPH,
    ],
  },
  challenge: {
    sides: [
      BLANK,
      FAILURE,
      FAILURE,
      FAILURE_FAILURE,
      FAILURE_FAILURE,
      THREAT,
      THREAT,
      FAILURE_THREAT,
      FAILURE_THREAT,
      THREAT_THREAT,
      THREAT_THREAT,
      DESPAIR,
    ],
  },
  force: {
    sides: [
      DARK,
      DARK,
      DARK,
      DARK,
      DARK,
      DARK,
      DARK_DARK,
      LIGHT,
      LIGHT,
      LIGHT_LIGHT,
      LIGHT_LIGHT,
      LIGHT_LIGHT,
    ],
  },
};

declare type FormControlElement = HTMLInputElement | HTMLTextAreaElement;

declare type DiceNames =
  | 'boost'
  | 'setback'
  | 'ability'
  | 'difficulty'
  | 'proficiency'
  | 'challenge'
  | 'force';

const capitalizeFirstLetter = (string: string) =>
  string.charAt(0).toUpperCase() + string.slice(1);

const sumSides = (...sides: DiceSide[]): DiceSide => {
  return sides.reduce((a, b) => {
    return {
      success: a.success + b.success,
      advantage: a.advantage + b.advantage,
      failure: a.failure + b.failure,
      threat: a.threat + b.threat,
      triumph: a.triumph + b.triumph,
      despair: a.despair + b.despair,
      dark: a.dark + b.dark,
      light: a.light + b.light,
    };
  }, BLANK);
};

const sumResults = ({
  success,
  advantage,
  failure,
  threat,
  triumph,
  despair,
  dark,
  light,
}: DiceSide): DiceSide => {
  return {
    success: Math.max(success + triumph - (failure + despair), 0),
    advantage: Math.max(advantage - threat, 0),
    failure: Math.max(failure + despair - (success + triumph), 0),
    threat: Math.max(threat - advantage, 0),
    triumph: Math.max(triumph - despair, 0),
    despair: Math.max(despair - triumph, 0),
    dark,
    light,
  };
};

const rollDice = (dice: Dice): DiceSide =>
  dice.sides[Math.floor(Math.random() * dice.sides.length)];

const resultToString = (result: DiceSide): string => {
  return (
    Object.entries(result)
      .map(([key, value]) => (value ? pluralize(key, value, true) : ''))
      .filter((value) => !!value)
      .join(', ') || 'Wash!'
  );
};

function App() {
  const initialState = {
    boost: 0,
    setback: 0,
    ability: 0,
    difficulty: 0,
    proficiency: 0,
    challenge: 0,
    force: 0,
  };
  const [result, setResult] = useState(BLANK);
  const [diceState, setDiceState] = useState(initialState);

  const handleRoll: React.FormEventHandler<HTMLFormElement> = (event) => {
    event.preventDefault();
    let rolls: DiceSide[] = [];

    Object.keys(dice).forEach((diceType) => {
      for (let i = 0; i < diceState[diceType as DiceNames]; i++) {
        rolls.push(rollDice(dice[diceType as DiceNames]));
      }
    });

    setResult(sumSides(...rolls));
  };

  const handleSetDiceState: React.ChangeEventHandler<FormControlElement> = (
    event
  ) => {
    setDiceState({
      ...diceState,
      [event.target.name]: +event.target.value,
    });
  };

  const handleClearDice = () => {
    setDiceState(initialState);
  };

  return (
    <div className='App'>
      <br />
      <Container>
        <Form onSubmit={handleRoll}>
          <Row xs={2} xxl={2}>
            {Object.keys(dice).map((diceType) => {
              return (
                <Form.Group key={diceType + 'formgroup'}>
                  <Form.Label>
                    <Dice variant={diceType as DiceNames} />
                    {` ${capitalizeFirstLetter(diceType)} Die`}
                  </Form.Label>
                  <Form.Control
                    type='number'
                    min={0}
                    step={1}
                    value={diceState[diceType as DiceNames]}
                    name={diceType}
                    onChange={handleSetDiceState}
                  />
                </Form.Group>
              );
            })}
          </Row>
          <br />
          <Row xs='auto'>
            <Col>
              <Button type='submit'>Roll</Button>
            </Col>
            <Col>
              <Button onClick={handleClearDice} variant='outline-primary'>
                Clear Dice
              </Button>
            </Col>
          </Row>
        </Form>
        <Row>
          <Col>The dice rolled: {resultToString(result)}</Col>
        </Row>
        <Row>
          <Col>The end result: {resultToString(sumResults(result))}</Col>
        </Row>
        <Row>
          <Col>
            * Sucesses from triumphs have been calculated as additional
            successes.
          </Col>
        </Row>
        <Row>
          <Col>
            ** Failures from despairs have been calculated as additional
            failures.
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default App;
